import React from 'react';
import '../styles/phonebook.css';

const FilterRecords = ({ persons, filter, handleDelete }) => {
  return (
    <table>
      <tbody>
        {persons
          .filter((person) => person.name.includes(filter))
          .map((person) => (
            <tr key={person.name}>
              <td>{person.name}</td>
              <td>{person.number}</td>
              <td>
                <button
                  value={person.id}
                  key={person.id}
                  onClick={() => {
                    if (
                      window.confirm(
                        `Are you sure you wish to delete ${person.name}?`
                      )
                    )
                      handleDelete(person.id);
                  }}
                >
                  Delete
                </button>
              </td>
            </tr>
          ))}
      </tbody>
    </table>
  );
};

export default FilterRecords;
