import React, { useState, useEffect } from 'react';
import crudOperations from './services/crud';
import AddPersonForm from './components/AddPersonForm';
import FilterRecords from './components/FilterRecords';
import ShowPersons from './components/ShowPersons';
import ErrorMessage from './components/ErrorMessage';

const App = () => {
  const [persons, setPersons] = useState([]);
  const [newName, setNewName] = useState('');
  const [newNumber, setNewNumber] = useState('');
  const [filter, setFilter] = useState('');
  const [errorMsg, setErrorMsg] = useState(null);

  useEffect(() => {
    console.log('effect');
    crudOperations
      .getAll()
      .then((numbersFromDB) => {
        console.log('promise fulfilled');
        setPersons(numbersFromDB);
      })
      .catch((err) => {
        console.log('ERROR: could not fetch data');
      });
  }, []);
  console.log('render', persons.length, 'persons');

  const addNumber = (event) => {
    event.preventDefault();
    const nameObject = {
      name: newName,
      number: newNumber,
    };
    const nameMatch = persons.find(({ name }) => name === newName);
    console.log('Name already in DB?', nameMatch);
    if (nameMatch) {
      changeNumber(nameMatch);
    } else {
      crudOperations
        .addRecord(nameObject)
        .then((returnedRecord) => {
          console.log('SUCCESS: number added;');
          setPersons(persons.concat(returnedRecord));
          setNewName('');
          setNewNumber('');
        })
        .catch((error) => {
          console.log('ERROR: could not addNumber');
          console.log('tähän error message: ', error.response.data.error);
          setErrorMsg(error.response.data.error);
          setTimeout(() => {
            setErrorMsg(null);
          }, 5000);
        });
    }
  };

  const changeNumber = (oldName) => {
    if (
      window.confirm(
        `${newName} is already added to the phonebook, replace the old number with a new one?`
      )
    ) {
      const nameObject = {
        name: newName,
        number: newNumber,
      };

      console.log('ja nimi on ', oldName.name, oldName.id);

      crudOperations
        .updateRecord(oldName.id, nameObject)
        .then((returnedRecord) => {
          console.log('SUCCESS: number UPDATED;');
          setPersons(
            persons.map((person) =>
              person.name !== newName ? person : returnedRecord
            )
          );
          setNewName('');
          setNewNumber('');
        })
        .catch((err) => {
          console.log('ERROR: could not UPDATE Number');
        });
    }
  };

  const handleDelete = (person) => {
    crudOperations.deleteRecord(person).then(
      crudOperations.getAll().then((numbersFromDB) => {
        setPersons(numbersFromDB);
      })
    );
  };

  const handleFilterChange = (event) => {
    console.log(event.target.value);
    setFilter(event.target.value);
  };

  const handlePersonChange = (event) => {
    console.log(event.target.value);
    setNewName(event.target.value);
  };

  const handleNumberChange = (event) => {
    console.log(event.target.value);
    setNewNumber(event.target.value);
  };

  return (
    <div>
      <h2>Phonebook</h2>

      <ShowPersons handleFilterChange={handleFilterChange} />

      <h2>Add new number</h2>

      <AddPersonForm
        {...{ addNumber, handleNumberChange, handlePersonChange }}
      />
      <ErrorMessage errorMsg={errorMsg} />

      <h2>Numbers</h2>

      <FilterRecords {...{ persons, filter, handleDelete }} />
    </div>
  );
};

export default App;
